import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import APTable from '../common/APTable';
import APButton from '../common/APButton';
import APSegmentedButton from '../common/APSegmentedButton/APSegmentedButton';
import APInlineButton from '../common/APInlineButton/APInlineButton';
import { styleNames } from '../../propTypes/commonComponent';
import { formatDate, formatTime } from '../../helpers/dateHelper';
import { formatNumberToLocale } from '../../helpers/numberFormatter';

import './TransferComponents.css';

const baseClasses = getBEMClasses('transfers');

class TransfersComponent extends React.Component {
  constructor() {
    const tabs = {
      sentTransfers: 'sentTransfers',
      receivedTransfers: 'receivedTransfers',
      sentRequests: 'sentRequests',
      receivedRequests: 'receivedRequests'
    };
    super();
    this.state = {
      tabs,
      selectedTab: tabs.sentTransfers
    };
  }

  getAmountRow = ({ Amount, product }) =>
    this.getMultilineCell(
      formatNumberToLocale(Amount, product.DecimalPlaces),
      product.ProductFullName
    );

  getDateRow = ({ CreatedTimestamp }) =>
    this.getMultilineCell(
      formatDate(CreatedTimestamp),
      formatTime(CreatedTimestamp)
    );

  getMultilineCell = (primarytext, secondaryText) => (
    <div className={baseClasses('multiline-col')}>
      <div className={baseClasses('primary-text')}>{primarytext}</div>
      <div className={baseClasses('secondary-text')}>{secondaryText}</div>
    </div>
  );

  getCell = field => row => (
    <div className={baseClasses('primary-text')}>{row[field]}</div>
  );

  getActionButtons = row => {
    if (row.Status === 'TransferCompleted' || row.Status === 'PayerRejected') {
      return null;
    }

    return (
      <div>
        <APButton
          styleName={styleNames.additive}
          onClick={() => this.allowRequest(row)}>
          {this.context.t('Approve')}
        </APButton>
        &nbsp;
        <APButton
          styleName={styleNames.subtractive}
          onClick={() => this.rejectRequest(row)}>
          {this.context.t('Reject')}
        </APButton>
      </div>
    );
  };

  getTransfersColumns = () => [
    {
      header: this.context.t('Action'),
      classModifier: 'transfers-action',
      cell: row => {
        const { tabs, selectedTab } = this.state;
        const isSent = selectedTab === tabs.sentTransfers;
        const sent = `${this.context.t('Sent')} ${row.product.ProductFullName}`;
        const received = `${this.context.t('Received')} ${
          row.product.ProductFullName
        }`;
        const from = `${this.context.t('From')} ${row.SenderUserName}`;
        const to = `${this.context.t('To')} ${row.ReceiverUserName}`;

        return isSent
          ? this.getMultilineCell(sent, to)
          : this.getMultilineCell(received, from);
      }
    },
    {
      header: this.context.t('Amount'),
      cell: this.getAmountRow,
      classModifier: 'amount'
    },
    {
      header: this.context.t('Note'),
      cell: this.getCell('Notes')
    }
  ];

  allowRequest = req => {
    this.props.approveTransferRequest(req.RequestCode);
  };

  rejectRequest = req => {
    this.props.rejectTransferRequest(req.RequestCode);
  };

  getRequestsSentColumns = () => [
    {
      header: this.context.t('Recipient'),
      classModifier: 'recipient',
      cell: row =>
        this.getMultilineCell(
          `${this.context.t('You requested to Receive')} ${
            row.product.ProductFullName
          }`,
          `${this.context.t('To')} ${row.PayerUsername}`
        )
    },
    {
      header: this.context.t('Amount'),
      classModifier: 'amount',
      cell: this.getAmountRow
    },
    {
      header: this.context.t('Time'),
      classModifier: 'time',
      cell: this.getDateRow
    },
    {
      header: this.context.t('Note'),
      classModifier: 'note',
      cell: this.getCell('Notes')
    },
    {
      header: this.context.t('Status'),
      classModifier: 'status',
      cell: this.getCell('Status')
    }
  ];

  getRequestsReceivedColumns = () => [
    {
      header: this.context.t('Sent By'),
      classModifier: 'sent-by',
      cell: row =>
        this.getMultilineCell(
          `${this.context.t('Request for you to send')} ${
            row.product.ProductFullName
          }`,
          `${this.context.t('From')} ${row.RequestorUsername}`
        )
    },
    {
      header: this.context.t('Amount'),
      classModifier: 'amount',
      cell: this.getAmountRow
    },
    {
      header: this.context.t('Time'),
      classModifier: 'time',
      cell: this.getDateRow
    },
    {
      header: this.context.t('Note'),
      classModifier: 'note',
      cell: this.getCell('Notes')
    },
    {
      header: this.context.t('Status'),
      classModifier: 'status',
      cell: this.getCell('Status')
    },
    {
      header: this.context.t('Action'),
      cell: this.getActionButtons
    }
  ];

  getColumns = selectedTab => {
    const columnsMethods = {
      sentTransfers: this.getTransfersColumns,
      receivedTransfers: this.getTransfersColumns,
      sentRequests: this.getRequestsSentColumns,
      receivedRequests: this.getRequestsReceivedColumns
    };

    return columnsMethods[selectedTab]();
  };

  onTabChange = selectedTab => {
    this.setState({ selectedTab });
  };

  render() {
    const { tabs, selectedTab } = this.state;
    const columns = this.getColumns(selectedTab);
    const items = this.props[selectedTab];
    const { getTransfers, OMSId, AccountId, usePagination } = this.props;

    return (
      <div className={baseClasses('wrapper')}>
        <div className={baseClasses('header-bar')}>
          <div className={baseClasses('header-text')}>
            {this.context.t('Transfers')}
          </div>
          <div className={baseClasses('select-buttons')}>
            <APSegmentedButton
              name="transfers-form-selector"
              defaultValue={selectedTab}
              items={[
                {
                  value: tabs.sentTransfers,
                  text: this.context.t('Sent Transfers'),
                  onClick: () => this.onTabChange(tabs.sentTransfers)
                },
                {
                  value: tabs.receivedTransfers,
                  text: this.context.t('Received Transfers'),
                  onClick: () => this.onTabChange(tabs.receivedTransfers)
                },
                {
                  value: tabs.sentRequests,
                  text: this.context.t('Sent Requests'),
                  onClick: () => this.onTabChange(tabs.sentRequests)
                },
                {
                  value: tabs.receivedRequests,
                  text: this.context.t('Received Requests'),
                  onClick: () => this.onTabChange(tabs.receivedRequests)
                }
              ]}
              styleName={styleNames.additive}
              customClass={baseClasses()}
              onClick={this.props.resetActivePage}
            />
          </div>
          <APInlineButton
            customClass={baseClasses('refresh-transfers')}
            icon="refresh"
            text={this.context.t('Refresh Transfers')}
            onClick={() => getTransfers(OMSId, AccountId)}
          />
        </div>
        <APTable
          {...{
            componentName: 'TransfersComponent',
            columns,
            pageSize: 6,
            minRow: 6,
            rows: items,
            baseClass: baseClasses,
            headerOutside: true,
            usePagination,
            empty: this.context.t('No data is available')
          }}
        />
      </div>
    );
  }
}

TransfersComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default TransfersComponent;



