import React, { Component } from 'react';
import PropTypes from 'prop-types';
import APIcon from '../APIcon/APIcon';
import config from '../../../config';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import { generateNumberRangeArray } from '../../../helpers/paginationHelper';

import './Pagination.css';

export default class PaginationComponent extends Component {
  static propTypes = {
    totalPages: PropTypes.number.isRequired,
    content: PropTypes.func.isRequired,
    activePage: PropTypes.number,
    containerClassName: PropTypes.string,
    componentName: PropTypes.string,
    customClass: PropTypes.string
  };

  static contextTypes = {
    t: PropTypes.func.isRequired
  };

  static defaultProps = {
    activePage: 0,
    containerClassName: '',
    customClass: ''
  };

  componentDidUpdate() {
    const { content, activePage, setActivePage } = this.props;
    const currentContent = content(activePage);

    if (activePage > 0 && currentContent.length === 0) {
      setActivePage(activePage - 1);
    }
  }

  getClasses = () => {
    return getBEMClasses([
      'pagination',
      `${this.props.customClass}-pagination`
    ]);
  };

  goToPage = (event, page) => {
    event.preventDefault();
    this.props.setActivePage && this.props.setActivePage(page);
  };

  renderNextButtons = page => {
    const paginationClasses = this.getClasses();
    const disabled =
      this.props.totalPages === 0 || page === this.props.totalPages - 1;
    return (
      <span className={paginationClasses('next')}>
        <a
          className={paginationClasses('text', { disabled })}
          onClick={e => !disabled && this.goToPage(e, page + 1)}>
          {this.context.t('Next')}
          <APIcon
            name="caretNext"
            customClass={paginationClasses('arrow', 'next')}
          />
        </a>
        <a
          className={paginationClasses('text', { disabled })}
          onClick={e =>
            !disabled && this.goToPage(e, this.props.totalPages - 1)
          }>
          <APIcon
            name="doubleCaretNext"
            customClass={paginationClasses('arrow', 'next')}
          />
        </a>
      </span>
    );
  };

  renderPreviousButtons = page => {
    const paginationClasses = this.getClasses();
    const disabled = this.props.totalPages === 0 || page === 0;
    return (
      <span className={paginationClasses('previous')}>
        <a
          className={paginationClasses('text', { disabled })}
          onClick={e => !disabled && this.goToPage(e, 0)}>
          <APIcon
            name="doubleCaretPrevious"
            customClass={paginationClasses('arrow', 'previous')}
          />
        </a>
        <a
          className={paginationClasses('text', { disabled })}
          onClick={e => !disabled && this.goToPage(e, page - 1)}>
          <APIcon
            name="caretPrevious"
            customClass={paginationClasses('arrow', 'previous')}
          />
          {this.context.t('Prev')}
        </a>
      </span>
    );
  };

  render() {
    const paginationClasses = this.getClasses();
    const { activePage, totalPages } = this.props;
    const { maxPages } = config.Pagination;
    const length = maxPages > totalPages ? totalPages : maxPages;

    const pages = generateNumberRangeArray(length, activePage, totalPages).map(
      i => (
        <a
          key={i}
          className={paginationClasses(
            'page',
            activePage === i ? 'active' : null
          )}
          onClick={e => this.goToPage(e, i)}>
          {i + 1}
        </a>
      )
    );

    const content = this.props.content(activePage);

    return (
      <div className={this.props.containerClassName}>
        <div className={paginationClasses('content')}>{content}</div>
        <div className={paginationClasses('container')}>
          {this.renderPreviousButtons(activePage)}
          <div className={paginationClasses('pages')}>{pages}</div>
          {this.renderNextButtons(activePage)}
        </div>
      </div>
    );
  }
}



