import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { Field } from 'redux-form';
import { styleNames, styleNamePropType } from '../../../propTypes/commonComponent';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import { getDataTestAttribute } from '../../../helpers/dataTestAttributeHelper';

import './APRadioTab.css';

const RadioTab = props => {
  const {
    items,
    input,
    baseClass,
    customClass,
    styleName,
    onClick,
    defaultValue
  } = props;

  const baseClasses = getBEMClasses(baseClass);
  const customClasses = getBEMClasses(customClass);

  return (
    <div
      className={classnames(
        'btn-group btn-group-toggle',
        baseClasses(),
        customClasses('rt-wrapper')
      )}
      data-toggle="buttons">
      {items.map((item, index) => {
        const inputValueIsSet = input.value || typeof input.value === 'boolean';
        const checked =
          item.value === (inputValueIsSet ? input.value : defaultValue);
        return (
          <label
            className={classnames(
              'btn',
              getLabelClass(
                baseClasses,
                'label',
                item.styleName || styleName,
                checked
              ),
              getLabelClass(
                customClasses,
                'rt-label',
                item.styleName || styleName,
                checked
              )
            )}
            key={item.value}
            onClick={onClick}
            data-test={getDataTestAttribute(item, 'text')}>
            <input
              type="radio"
              id={item.value.toString()}
              {...input}
              checked={checked}
              value={item.value}
              onClick={item.onClick}
              onBlur={e => e.preventDefault()}
              onChange={e => {
                const value =
                  typeof item.value === 'boolean'
                    ? e.target.value === 'true'
                    : e.target.value;
                input.onChange(value);
              }}
            />
            {item.text}
          </label>
        );
      })}
    </div>
  );
};

const getLabelClass = (bem, element, styleName, isChecked) => {
  return bem({
    element,
    modifiers: classnames(styleName, {
      checked: isChecked,
      [`checked-${styleName}`]: isChecked
    })
  });
};

const itemType = PropTypes.shape({
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool
  ]).isRequired,
  text: PropTypes.string.isRequired,
  dataTest: PropTypes.string
});

RadioTab.propTypes = {
  items: PropTypes.arrayOf(itemType).isRequired,
  baseClass: PropTypes.string,
  customClass: PropTypes.string,
  onClick: PropTypes.func,
  defaultValue: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool
  ]),
  styleName: styleNamePropType
};

RadioTab.defaultProps = {
  baseClass: 'ap-radio-tab',
  styleName: styleNames.general
};

const APRadioTab = props => <Field component={RadioTab} {...props} />;

export default APRadioTab;



