import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import APButton from '../APButton';
import APIcon from '../APIcon/APIcon';

import './APInlineButton.css';

const baseClass = 'ap-inline-btn';

const APInlineButton = props => {
  const { icon, text, customClass, iconModifier, ...otherProps } = props;

  const bemClasses = getBEMClasses([baseClass, customClass]);

  return (
    <APButton baseClass={baseClass} customClass={customClass} {...otherProps}>
      <APIcon
        name={icon}
        classModifiers={iconModifier}
        customClass={bemClasses('image')}
      />
      <span className={bemClasses('text')}>{text}</span>
    </APButton>
  );
};

APInlineButton.propTypes = {
  icon: PropTypes.string.isRequired,
  iconModifier: PropTypes.string,
  text: PropTypes.string.isRequired,
  customClass: PropTypes.string
};

APInlineButton.defaultProps = {
  customClass: 'custom-inline-btn'
};

export default APInlineButton;



