import { createSelector } from 'reselect';
import get from 'lodash/get';

export const transfersByProductIdSelector = createSelector(
  [
    state => state.transferRequest.items,
    state => state.transferRequestRequested.items,
    state => state.transfer.transfers,
    state => state.transfer.transfersReceived,
    state => state.position.selectedProductId
  ],
  (
    receivedRequests,
    sentRequests,
    sentTransfers,
    receivedTransfers,
    selectedProductId
  ) => {
    const filterFn = item => item.ProductId === selectedProductId;

    return {
      receivedRequests: sortTransfersByDate(receivedRequests.filter(filterFn)),
      sentRequests: sortTransfersByDate(sentRequests.filter(filterFn)),
      sentTransfers: sentTransfers.filter(filterFn),
      receivedTransfers: receivedTransfers.filter(filterFn)
    };
  }
);

export const transfersRequestSelector = createSelector(
  state => state.transferRequest.items,
  transferRequestItems => {
    const filteredItems = Array.isArray(transferRequestItems)
      ? transferRequestItems.filter(item => item.Status === 'Requested')
      : [];

    return filteredItems;
  }
);

const sortTransfersByDate = transfers => {
  return transfers.sort((a, b) => {
    const firstTimestamp = get(a, 'CreatedTimestamp');
    const secondTimestamp = get(b, 'CreatedTimestamp');
    if (firstTimestamp > secondTimestamp) {
      return -1;
    } else if (firstTimestamp < secondTimestamp) {
      return 1;
    } else {
      return 0;
    }
  });
};



