import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import TransfersComponent from './TransfersComponent';
import {
  approveTransferRequest,
  rejectTransferRequest,
  getTransferRequestsReceived,
  getTransferRequestsRequested
} from '../../redux/actions/transferRequestActions';
import {
  getTransfers,
  getIncomingTransfers
} from '../../redux/actions/transferActions';
import { setActivePage } from '../../redux/actions/paginationActions';
import { transfersByProductIdSelector } from '../../redux/selectors/transfersTableSelectors';
import config from '../../config';

const mapStateToProps = state => {
  const {
    receivedRequests,
    sentRequests,
    sentTransfers,
    receivedTransfers
  } = transfersByProductIdSelector(state);
  const { products } = state.product;
  const { OMSId, AccountId } = state.user.userInfo;
  const { usePagination } = config.OrderHistoryData;

  const addProduct = item =>
    (item.product = products.find(p => item.ProductId === p.ProductId));

  receivedRequests.forEach(addProduct);
  sentRequests.forEach(addProduct);
  sentTransfers.forEach(addProduct);
  receivedTransfers.forEach(addProduct);

  return {
    receivedRequests,
    sentRequests,
    sentTransfers,
    receivedTransfers,
    OMSId,
    AccountId,
    usePagination
  };
};

const mapDispatchToProps = dispatch => ({
  approveTransferRequest: requestCode =>
    dispatch(approveTransferRequest(requestCode)),
  rejectTransferRequest: requestCode =>
    dispatch(rejectTransferRequest(requestCode)),
  getTransfers: (OMSId, accountId) => {
    dispatch(getTransfers(OMSId, accountId));
    dispatch(getIncomingTransfers(OMSId, accountId));
    dispatch(getTransferRequestsReceived(accountId, OMSId));
    dispatch(getTransferRequestsRequested(accountId, OMSId));
  },
  resetActivePage: () =>
    dispatch(
      setActivePage({ componentName: 'TransfersComponent', activePage: 0 })
    )
});

const TransfersForm = reduxForm({ form: 'transfers-form' })(TransfersComponent);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TransfersForm);



